<template>
	<view class="wrap">
		<view class="u-tabs-box">
			<u-tabs-swiper activeColor="#06A7E2" inactive-color="#000000" ref="tabs" :list="list" :current="current"
				@change="change" bar-width="100" :is-scroll="false" swiperWidth="750">
			</u-tabs-swiper>
		</view>
		<swiper class="swiper-box" :current="swiperCurrent" @transition="transition" @animationfinish="animationfinish">
			<swiper-item>
				<!-- <scroll-view scroll-y style="height: 100%; width: 100%;">
					<view class="swiper-div">
						</view>
				     </scroll-view> -->
				<view class="swiper-item">
					<u-form :model="form1" ref="basicForm" label-width="200" label-align="center"
						:border-bottom="false">
						<u-form-item label="申请人" class="form1-item">
							<u-input v-model="form1.name" placeholder="请输入申请人姓名" type="text" border="border" height="40" />
						</u-form-item>
						<u-form-item label="申请部门" class="form1-item">
							<u-input v-model="form1.depart" placeholder="请输入申请部门" type="text" border="border" height="40" />
						</u-form-item>
						<u-form-item label="归属公司" class="form1-item">
							<u-input v-model="form1.company" placeholder="请输入归属公司" type="text" border="border" height="40" />
						</u-form-item>
						<u-form-item label="业务部门" class="form1-item">
							<u-input v-model="form1.depart2" placeholder="请输入业务部门" type="text" border="border" height="40" />
						</u-form-item>
						<u-form-item label="预算科目" class="form1-item">
							<u-input v-model="form1.subject" placeholder="请输入预算科目" type="text" border="border" height="40" />
						</u-form-item>
						<u-form-item label="业务说明" class="form1-item">
							<u-input v-model="form1.descrp" placeholder="请输入业务说明" type="text" border="border" height="40" />
						</u-form-item>
						<u-form-item label="结算方式" class="form1-item">
							<u-input v-model="form1.pattern" placeholder="请输入结算方式" type="text" border="border" height="40" />
						</u-form-item>
						<u-form-item label="金额" class="form1-item">
							<u-input v-model="form1.number" placeholder="请输入金额" type="text" border="border" height="40" />
						</u-form-item>
						<u-form-item label="研发订单" class="form1-item">
							<u-input v-model="form1.order" placeholder="请输入订单号" type="text" border="border" height="40" />
						</u-form-item>
					</u-form>
				</view>
			</swiper-item>
			<swiper-item>
				<view class="swiper-item">
					<u-form :model="form2" ref="cashForm" label-width="200" label-align="center">
						<u-form-item label="收款人" class="form2-item">
							<u-input v-model="form2.name" placeholder="请输入收款人姓名" type="text" border="border" height="60" />
						</u-form-item>
						<u-form-item label="收款银行" class="form2-item">
							<u-input v-model="form2.bank" placeholder="请输入收款银行" type="text" border="border" height="60" />
						</u-form-item>
						<u-form-item label="收款账号" class="form2-item">
							<u-input v-model="form2.account" placeholder="请输入收款账号" type="text" border="border" height="60" />
						</u-form-item>
						<u-form-item label="收款金额" class="form2-item">
							<u-input v-model="form2.number" placeholder="请输入收款金额" type="text" border="border" height="60" />
						</u-form-item>
						<u-form-item label="开户代码" class="form2-item">
							<u-input v-model="form2.code" placeholder="请输入开户代码" type="text" border="border" height="60" />
						</u-form-item>
						<u-form-item label="对公对私" class="form2-item">
							<u-select v-model="show" mode="single-column" :list="selectionList" @confirm="confirm"></u-select>
							<u-input v-model="selectLabel" type="select" @click="show = true" />
						</u-form-item>
					</u-form>
				</view>
			</swiper-item>
		</swiper>
		<u-button type="primary" shape="square" @click="submit">提交</u-button>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				list: [{
						name: '基础信息'
					},
					{
						name: '收款信息'
					}
				],
				selectionList: [{
						value: '0',
						label: '对公'
					},
					{
						value: '1',
						label: '对私'
					},
				],
				form1: {
					name: '',
					dapart: '',
					company: '',
					depart2: '',
					subject: '',
					descrp: '',
					pattern: '',
					number: 0,
					order: ''
				},
				form2: {
					name: '',
					bank: '',
					account: '',
					number: 0,
					code: '',
					isCommon: ''
				},
				show: false,
				current: 0,
				swiperCurrent: 0,
				selectLabel: '对公'
			}
		},
		methods: {
			// tab栏切换
			change(index) {
				this.swiperCurrent = index;
				// this.getOrderList(index);
			},
			// swiper-item左右移动，通知tabs的滑块跟随移动
			transition(e) {
				let dx = e.detail.dx;
				this.$refs.tabs.setDx(dx);
			},
			// 由于swiper的内部机制问题，快速切换swiper不会触发dx的连续变化，需要在结束时重置状态
			// swiper滑动结束，分别设置tabs和swiper的状态
			animationfinish(e) {
				let current = e.detail.current;
			  this.$refs.tabs.setFinishCurrent(current);
			  this.swiperCurrent = current;
			  this.current = current;
			  },
			confirm(e) {
				this.selectLabel = e[0].label;
				this.form2.isCommon = e[0].value;
				console.log(this.form2.isCommon);
			}
		}
	}
</script>

<style lang="scss" scoped>
	.form1-item {
		height: 40px;
	}

	.form2-item {
		height: 60px;
	}

	.wrap {
		width: 100%;
	}

	.swiper-box {
		width: 700rpx;
		background-color: #ffffff;
		margin: 20rpx auto;
		border-radius: 20rpx;
		box-sizing: border-box;
		padding: 10rpx;
		font-size: 28rpx;
		height: 900rpx;
	}

	.swiper-item {
		height: 100%;
	}
</style>
